import uniPopup from '@/components/uni-popup.vue'
export default {
	components: {
		uniPopup
	},
	data() {
		return {
		}
	},
	onNavigationBarButtonTap(e) {
		if(e.index == 0){
			this.togglePopup();
		}
	},
	methods: {
		togglePopup() {
			 this.$refs['wallet'].open();
		},
		canclePopup(){
			this.$refs['wallet'].close();
		},
		manage(){
			uni.navigateTo({
				url: 'backup'
			});
		}
	}
}
